
.signHome {
  margin-left: 200px;
  width: calc(100% - 200px);
}
.signHome .signHome_top {
  width: 100%;
  padding-left: 0.15rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-bottom: 1px solid #DEDEDE;
  height: 0.8rem;
}
.signHome .signHome_top ul li {
  width: 1.92rem;
  float: left;
  height: 0.8rem;
  line-height: 0.8rem;
  font-size: 0.28rem;
  text-align: center;
  color: #333;
}
.signHome .signHome_top ul li.active {
  border-bottom: 2px solid #3c9bff;
  color: #3c9bff;
}
.signHome .signHome_bottom {
  width: 100%;
  padding: 0.15rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.signHome .signHome_bottom .bottom_content {
  margin-top: 0.3rem;
  background-color: #ffffff;
  width: 30.5%;
  padding: 0.3rem 0 0.15rem 0.2rem;
  overflow: hidden;
  border: 1px solid #f0f6fe;
  -webkit-box-shadow: 0 0 12px #f3f7fd;
          box-shadow: 0 0 12px #f3f7fd;
}
.signHome .signHome_bottom .bottom_content .content_top {
  height: 90px;
  width: 100%;
  position: relative;
}
.signHome .signHome_bottom .bottom_content .content_top b {
  position: absolute;
  left: 1.6rem;
  top: 0.2rem;
  font-size: 0.28rem;
}
.signHome .signHome_bottom .bottom_content .content_top p {
  position: absolute;
  left: 1.6rem;
  bottom: 0.2rem;
  font-size: 0.24rem;
  color: #9c9c9c;
}
.signHome .signHome_bottom .bottom_content .content_top p span {
  vertical-align: middle;
}
.signHome .signHome_bottom .bottom_content .content_top p span:nth-child(2) {
  margin-left: 0.2rem;
}
.signHome .signHome_bottom .bottom_content .content_middle {
  margin-top: 0.3rem;
}
.signHome .signHome_bottom .bottom_content .content_middle span {
  display: inline-block;
  padding: 0.03rem 0.2rem;
  border-radius: 12px;
  color: #3c9bff;
  background-color: #ebf5ff;
  font-size: 0.2rem;
  font-weight: 700;
  margin-right: 0.15rem;
}
.signHome .signHome_bottom .bottom_content .content_middle span:nth-child(2) {
  background-color: #f6d8d6;
  color: #d43d34;
}
.signHome .signHome_bottom .bottom_content .content_bottom {
  margin-top: 0.2rem;
}
.signHome .signHome_bottom .bottom_content .content_bottom span {
  font-size: 0.2rem;
  color: #9F9F9F;
  padding-left: 0.05rem;
}
.signHome .signHome_bottom .bottom_content .content_bottom span:nth-child(2) {
  padding-left: 0.1rem;
}
